span.todo-load-no-todo-bw svg .cls-1 {
    fill: #13436D;
    transition: all .5s;
}
span.todo-load-no-todo-bw svg .cls-2 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-no-todo-wb svg .cls-1 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-no-todo-wb svg .cls-2 {
    fill: #13436D;
    transition: all .5s;
}
span.todo-load-no-todo svg {
    width: 100px;
    transition: all .5s;
}
span.todo-load-no-todo-wb svg {
    scale: 1.1;
    transition: all .5s;
}
span.todo-load-no-todo h2 {
    transition: all .5s;
}
span.todo-load-no-todo-wb h2 {
    scale: 1.1;
    transition: all .5s;
}
span.todo-load-no-todo {
    display: flex;    
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: calc(100% - 100px);
}


