ul {
    display: flex;
    position: relative;
    padding: 40px;
    flex-direction: column;
    box-shadow: 
    1px 1px 3px rgba(0, 0, 0, 0.5),
    1px -1px 3px rgba(0, 0, 0, 0.5),
    -1px -1px 3px rgba(0, 0, 0, 0.5),
    -1px 1px 3px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    width: 320px;
    background-color: #1E1E1E;
    height: calc(100vh - 300px);
}