span.todo-load-animation-bw svg .cls-1 {
    fill: #13436D;
    transition: all .5s;
}
span.todo-load-animation-bw svg .cls-2 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-animation-wb svg .cls-1 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-animation-wb svg .cls-2 {
    fill: #13436D; 
    transition: all .5s;
}
span.todo-load-animation svg {
    width: 100px;
    transition: all .5s;
}
span.todo-load-animation-wb svg {
    scale: 1.1;
    transition: all .5s;
}
span.todo-load-animation {
    display: flex;    
    justify-content: center;
    align-items: center;
    width: 100%;
    height: calc(100% - 100px);
}


