span.todo-load-error-wb svg .cls-1 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-error-wb svg .cls-2 {
    fill: #E1E1E0;
    transition: all .5s;
}
span.todo-load-error-bw svg .cls-1 {
    fill: #E1E1E0;
    opacity: .5;
    transition: all .5s;
}
span.todo-load-error-bw svg .cls-2 {
    fill: #E1E1E0;
    opacity: .5;
    transition: all .5s;
}
span.todo-load-error svg {
    width: 100px;
}
span.todo-load-error {
    display: flex;    
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: calc(100% - 100px);
}


